@keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

@keyframes fade-out {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}

@keyframes slide-up {
0% {
    transform: translateY(100%);
}
100% {
    transform: translateY(0%);
}
}


/* .dashboard-only[removing], .scrolly-only[removing] {
    animation: fade-out 500ms forwards;
} */

.dashboard-only[entering], .scrolly-only[entering] {
    animation: slide-up 1000ms forwards, fade-in 500ms
}
